.mynav{
   position: fixed !important;
   top:0px;
   left:0px;
   width: 100%;

   border-radius: 0px;
}
.mynav img{
    width: 50px  !important;
}
.ul-nav a{
    color: #fff !important;
     transition: color 0.6s;
}
 .ul-nav  a:hover{
    color: green !important;
}
.bt{
    color: #fff !important;
    transition: color 0.6s, background 0.6s;
}
.bt:hover{
    background-color: green !important;
    color: rgba(255, 255, 0, 0.849) !important;
}
@media screen and (max-width: 844px){
    .text h1{
        font-size: 30px;
    }
    .mynav {
       display: inline !important;
    }
    .mynav ul{
        display: inline-block;
    }
    .mynav ul li{
        display: inline-block;
        
    }
}
.landing-page{
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background-image:linear-gradient(rgba(0,0,0,0.9), rgba(0,0,0,0.3)), url('./img/showcase.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
   
}
.row1{
    margin-top: 30vh;
    text-align: center;
    color: green;
}