a, Link, input{
    color:green !important
}
.formcontainer{
    position: fixed;
    width: 50%;
    height:auto;
   top:20%;
   left: 10%;
   transform:scale(0.8);
}
.is-invalid{
    border: 2px solid red !important;
    box-shadow: 0 1px 1px 0 red;
}
.col4{
   
    padding: 2px;
}
label{
    color: rgb(24, 161, 24);
    font-weight: 550;
}
.lcard{
   padding:0px;
    margin:0% 0px;
    
    font-weight: 350;
    overflow:hidden
}
input:focus {
    background-color: #fff !important;
   
  }
.cside{
    height: 100%;
    background: linear-gradient(rgba(0,0,0,0.8), rgba(0,0,0,0.5)) ,url('/img/nda.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    transform: scale(1.1);
   
    text-align: center;
}
.cside img{
    transform: scale(0.9)
}
.form-child{
    margin-top: 20px;
   
}
.cside p{color: #fff !important}
@media  screen and (max-width: 1550px) {
    /* For tablets: */
    .formcontainer{
        width: 80%;

    }

  }
  @media  screen and (max-width: 950px) {
    .spann{
        display: none;
    }
  }

  @media  screen and (max-width: 844px) {
    #h3, #p{
       margin: 0px !important;
    }

  }