body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a, Link, input{
    color:green !important
}
.formcontainer{
    position: fixed;
    width: 50%;
    height:auto;
   top:20%;
   left: 10%;
   -webkit-transform:scale(0.8);
           transform:scale(0.8);
}
.is-invalid{
    border: 2px solid red !important;
    box-shadow: 0 1px 1px 0 red;
}
.col4{
   
    padding: 2px;
}
label{
    color: rgb(24, 161, 24);
    font-weight: 550;
}
.lcard{
   padding:0px;
    margin:0% 0px;
    
    font-weight: 350;
    overflow:hidden
}
input:focus {
    background-color: #fff !important;
   
  }
.cside{
    height: 100%;
    background: linear-gradient(rgba(0,0,0,0.8), rgba(0,0,0,0.5)) ,url('/img/nda.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
   
    text-align: center;
}
.cside img{
    -webkit-transform: scale(0.9);
            transform: scale(0.9)
}
.form-child{
    margin-top: 20px;
   
}
.cside p{color: #fff !important}
@media  screen and (max-width: 1550px) {
    /* For tablets: */
    .formcontainer{
        width: 80%;

    }

  }
  @media  screen and (max-width: 950px) {
    .spann{
        display: none;
    }
  }

  @media  screen and (max-width: 844px) {
    #h3, #p{
       margin: 0px !important;
    }

  }
.mynav{
   position: fixed !important;
   top:0px;
   left:0px;
   width: 100%;

   border-radius: 0px;
}
.mynav img{
    width: 50px  !important;
}
.ul-nav a{
    color: #fff !important;
     transition: color 0.6s;
}
 .ul-nav  a:hover{
    color: green !important;
}
.bt{
    color: #fff !important;
    transition: color 0.6s, background 0.6s;
}
.bt:hover{
    background-color: green !important;
    color: rgba(255, 255, 0, 0.849) !important;
}
@media screen and (max-width: 844px){
    .text h1{
        font-size: 30px;
    }
    .mynav {
       display: inline !important;
    }
    .mynav ul{
        display: inline-block;
    }
    .mynav ul li{
        display: inline-block;
        
    }
}
.landing-page{
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background-image:linear-gradient(rgba(0,0,0,0.9), rgba(0,0,0,0.3)), url(/static/media/showcase.05f83c0f.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
   
}
.row1{
    margin-top: 30vh;
    text-align: center;
    color: green;
}


