.navbar.navbar-absolute {

  padding-top: 0px !important;
 
}
.card-header-primary, .card-header,.card-title ,.card{
  z-index: 0 !important;
}
.dropdown-item:hover{
  background-color: #28a745 !important;
  color:#fff !important;
}
.content{
  z-index: -360 !important;
}

.fixed-top {
  position: fixed !important;
  top:0 !important;
  /*background-color: #fff !important;*/
  z-index: 1 !important;
}
.contfluid{
  background-color: #fff;
   margin-top: 0px;
}
.btn-primary{
  background-color:#28a745 !important; 
  color:#fff !important;
}
.logo img{
  transform: scale(1.2);
  margin:0px;
  padding: 0px;
  width: 80px;
  height: auto;
}
#wrapper{
  margin-top: -20px !important;
  padding: 0px !important;

}


.card .card-header-primary .card-icon, .card .card-header-primary .card-text, .card .card-header-primary:not(.card-header-icon):not(.card-header-text), .card.bg-primary, .card.card-rotate.bg-primary .front, .card.card-rotate.bg-primary .back {
  background: linear-gradient(60deg, #28a745, #28a745) !important;
}
.form-control, .is-focused .form-control {
  background-image: linear-gradient(to top, #28a745 2px, rgba(156, 39, 176, 0) 2px), linear-gradient(to top, #d2d2d2 1px, rgba(210, 210, 210, 0) 1px) !important;
}

/*@media (max-width: 991px){}*/
strong{
  color: #28a745
}

.list-group-item {
  position: relative !important;
  display: block !important;
  padding: .75rem 1.25rem !important;
  margin-bottom: -1px !important;
  background-color: #fff !important;
  border: 1px solid rgba(0, 0, 0, 0.178) !important;
}

@media screen and (max-width: 991px){
  .nav-open .main-panel, .nav-open .wrapper-full-page, .nav-open .navbar .container .navbar-toggler, .nav-open .navbar .container .navbar-wrapper, .nav-open .navbar .container {
    left: 0;
    -webkit-transform: translate3d(-260px, 0, 0);
    -moz-transform: translate3d(-260px, 0, 0);
    -o-transform: translate3d(-260px, 0, 0);
    -ms-transform: translate3d(-260px, 0, 0);
    transform: translate3d(-240px, 0, 0) !important;
    z-index: 0 !important;
   
}
